body {
  margin: 0;
}

li {
  list-style: none;
}

h2, p, ul {
  margin: 0;
  padding: 0;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, .4);
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.footer-modal {
  width: 800px;
  margin: auto;
}

.student__list {
  padding-top: 50px;
}

.students-box {
  height: 500px;
  background: linear-gradient(135deg, #767681, #222235) 0 0 / contain no-repeat;
  border-radius: 25px;
  margin: auto;
}

.footer__list-outside {
  width: 170px;
  border: 2px solid #4a4a6b;
  border-radius: 50%;
  margin: 0 auto 10px;
  padding: 7px;
}

.footer__list-inside {
  border: 2px solid #4b5568;
  border-radius: 50%;
  padding: 7px;
}

.student-foto {
  width: 150px;
  border-radius: 50%;
}

.students-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.students-info__title {
  letter-spacing: .1em;
  color: #8991b8;
  border-bottom: 2px solid #8991b8;
  margin-bottom: 12px;
  padding-bottom: 5px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.students-info__text {
  letter-spacing: .1em;
  color: #fff;
  margin-bottom: 10px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.students-info__about {
  letter-spacing: .1em;
  color: #b4b1b1;
  text-align: center;
  margin-bottom: 10px;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

